.App {
  text-align: center;
}
.App-link {
  color: #61dafb;
}
.signs button{
  cursor: pointer;
}
.signs button:nth-of-type(1){
  margin-left: 0;
  padding-left: 0;
}
.score{
  text-align: center;
}
.you{
  min-width:240px;
  min-height: 240px;
  background-color:tomato;
  border: 1px solid black;
}
.computer{
  min-width:240px;
  min-height: 240px;
  background-color:teal;
  border: 1px solid black;
}
.you svg, .computer svg{
  margin-top: 33%;
}
.score{
  font-size: 27px;
  text-align: center;
}
.signs{
  margin-top: 5%;
}
.btn2{
  margin-top: 5%;
  margin-right: 7%;
  float: right;
}
@media (max-width: 700px){
  .you{
    min-width: 110px;
    min-height: 160px;
  }
  .computer{
    min-width:110px;
    min-height: 160px;
  }
  .btn1{
    margin-left: 50px;
    width: 80px;
  }
  .btn1:nth-of-type(0){
    margin-left: 0;
  }
  .you svg, .computer svg{
    margin-top: 23%;
  }
}